body.zolo-popup-fixed {
    overflow: hidden;
}

.wp-block-zolo-popup-builder {
    position: relative;

    &.info_bar {
        &.not-push {
            position: fixed;
            left: 0;
            width: 100%;
            z-index: 9999;
        }
        &.inp_top {
            top: 0;
        }
        &.inp_bottom {
            bottom: 0;
        }
    }

    &.popup_box {
        display: flex;
        &.zolo-popup-overlay {
            background-color: rgba(62, 62, 62, 0.7);
            &.frontend {
                position: fixed;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 9999;
                overflow-y: scroll;
                min-height: 100vh;
            }
        }
        &.pbp_top_left {
            align-items: flex-start;
            justify-content: left;
        }

        &.pbp_top_center {
            align-items: flex-start;
            justify-content: center;
        }

        &.pbp_top_right {
            align-items: flex-start;
            justify-content: right;
        }

        &.pbp_center_left {
            align-items: center;
            justify-content: flex-start;
        }

        &.pbp_center_center {
            align-items: center;
            justify-content: center;
        }

        &.pbp_center_right {
            align-items: center;
            justify-content: flex-end;
        }

        &.pbp_bottom_left {
            align-items: flex-end;
            justify-content: left;
        }

        &.pbp_bottom_center {
            align-items: flex-end;
            justify-content: center;
        }

        &.pbp_bottom_right {
            align-items: flex-end;
            justify-content: right;
        }
    }

    .zolo-popup-inner {
        background-color: #fff;
        border-radius: 10px;
        overflow: hidden;
        &.popup_box {
            width: 600px;
            position: relative;
            box-sizing: border-box;
        }
    }

    .zolo-popup-close-btn {
        position: absolute;
        top: 0;
        cursor: pointer;
        margin: 25px;
        background-color: rgba(122, 122, 132, 0.34);
        border: 2px solid rgba(222, 222, 222, 0.38);
        padding: 8px;
        border-radius: 10px;
        transition: color 0.3s;
        backdrop-filter: blur(5px);
        &.cbp_top_left {
            left: 0;
        }
        &.cbp_top_right {
            right: 0;
        }
        svg {
            transition: color 0.3s;
            width: 16px;
            height: 16px;
            display: block;
            fill: #dadada;
        }
        &:hover {
            svg {
                fill: #fff;
            }
        }
        &:focus {
            border-color: #b4b0b0;
        }
    }
    .zolo-image-block-inner {
        width: 100%;
    }
}

.admin-bar {
    .wp-block-zolo-popup-builder {
        &.info_bar {
            &.inp_top {
                top: 32px;
            }
        }
    }
}

/* Responsive Visibilty */
@media screen and (min-width: 1025px) {
    .wp-block-zolo-popup-builder.zolo-popup-hod {
        display: none !important;
    }
}

/* Tablet View */
@media (max-width: 1024px) and (min-width: 768px) {
    .wp-block-zolo-popup-builder.zolo-popup-hot {
        display: none !important;
    }
}

/* Mobile View */
@media (max-width: 767px) {
    .wp-block-zolo-popup-builder.zolo-popup-hom {
        display: none !important;
    }
}
